#vue-samples{
    color: white;
    text-align: right;
}

#vue-samples div{
    padding-left: 50px;
}

#vue-logo{
    width: 230px;
}

/* Mobile Adjusts */
@media (max-width: 576px){
    #vue-samples{
        text-align: center;
    }

    #vue-samples div{
        padding-left: 0px;
    }    

    #vue-logo{
        margin-top: 40px;
    }
}